import Vue from 'vue';
import VueRouter from 'vue-router';
import Game from '../views/Game';
// import Players from '../views/Players';

Vue.use(VueRouter);

const routes = [{
    path: '/',
    name: 'Game',
    component: Game
  },
  // {
  //   path: '/players',
  //   name: 'Players',
  //   component: Players
  // },
];

const router = new VueRouter({
  mode: 'history',
  // base: process.env.BASE_URL,
  baseUrl: process.env.VUE_APP_MODE === 'production' ? 'https://tjallo.github.io/' : '/',

  routes
});

export default router;